.random-planet {
    display: -ms-flexbox;
    display: flex;
    padding: 1rem;
    min-height: 150px;
}
.random-planet .planet-image {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    margin-right: 1rem;
}
.random-planet .list-group {
    margin: 1rem 0 0 1rem;
}
.random-planet .list-group-item {
    padding: .25rem;
}
.random-planet .list-group-item .term {
    margin-right: .5rem;
}



